import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Chip from '@material-ui/core/Chip';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import replyLine from '../../images/ico_notice.png';

const styles = theme => ({
  root: {
    backgroundColor: '#f5f5f5',
    padding: '0 15px 26px'
  },
  nested: {
    paddingLeft: '0',
    paddingRight: '0'
  },
  chip: {
    backgroundColor: '#aaa',
    width: '45px',
    height: '25px',
    fontSize: 12,
    color: '#fff',
    borderRadius: '12.5px',
    marginLeft: '21px',
    position: 'relative',
    '&:before': {
      content: "''",
      display: 'block',
      width: '14.5px',
      height: '14.5px',
      borderLeft: '1px solid #aaa',
      borderBottom: '1px solid #aaa',
      position: 'absolute',
      top: '0',
      left: '-21px'
    }
  },
  detail: {
    fontSize: 14,
    paddingLeft: '20px'
  },
  btnDelete: {
    height: '38px',
    backgroundColor: '#f5f5f5',
    border: '1px solid #d5d5d5',
    fontSize: 14,
    color: '#666',
    marginTop: '25px'
  },
  replyDate: {
    fontSize: 11,
    color: '#aaa',
    marginLeft: '10px'
  }
});

class ContactReply extends React.Component {
  static defaultProps = {
    info: {
      id: 0,
      title: '크래커 베타 서비스 오픈합니다.',
      date: '2017.11.23',
      detail:
        '크래커 베타 서비스 오픈합니다./n 많은 서비스 이용 바랍니다./n 이용하시다가 불편하신 점은 언제든지 고객센터로 말씀해주세요./n 감사합니다./n'
    }
  };

  state = { open: false };

  handleClick = () => {
    this.setState({ open: !this.state.open });
  };

  render() {
    const { classes } = this.props;
    const { id, title, created, contents } = this.props.info;
    const replyDate = created.split(' ')[0].replace(/-/g, '.');

    return (
      <div className={classes.root}>
        <ListItem className={classes.nested}>
          <Chip label="답변" className={classes.chip} />
          <span className={classes.replyDate}>{replyDate}</span>
        </ListItem>
        <Typography className={classes.detail}>{contents}</Typography>
        <Button className={classes.btnDelete} fullWidth>
          삭제
        </Button>
      </div>
    );
  }
}

ContactReply.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ContactReply);



